<template>
  <div class="login">
    <md-card class="card blue-grey z-depth-1 card-login">
      <md-card-header>
        <img src="../assets/img/icon_admin.png" class="card-image" />
        <span class="card-title-login">Bienvenido</span>
      </md-card-header>

      <md-card-content>
        <div>
          <form novalidate class @submit.prevent="validateUser">
            <div class="md-layout md-gutter md-alignment-center">
              <div class="md-layout-item md-xsmall-size-90 md-size-70">
                <md-field :class="getValidationClass('email')">
                  <label for="email">Email</label>
                  <md-input
                    name="email"
                    id="email"
                    autocomplete="email"
                    type="email"
                    v-model="form.email"
                    :disabled="sending"
                  />
                  <span class="md-error" v-if="!$v.form.email.required">Porfavor ingrese un usuario</span>
                  <span class="md-error" v-else-if="!$v.form.email.email">Email incorrecto</span>
                </md-field>
              </div>
            </div>

            <div class="md-layout md-gutter md-alignment-center">
              <div class="md-layout-item md-xsmall-size-90 md-size-70">
                <md-field :class="getValidationClass('password')">
                  <label for="password">Contraseña</label>
                  <md-input
                    name="password"
                    id="password"
                    autocomplete="given-name"
                    type="password"
                    v-model="form.password"
                    :disabled="sending"
                  />
                  <span
                    class="md-error"
                    v-if="!$v.form.email.required"
                  >Por favor ingrese su contraseña</span>
                </md-field>
              </div>
            </div>

            <div class="md-layout md-gutter md-alignment-center">
              <md-progress-spinner
                :md-diameter="30"
                :md-stroke="3"
                md-mode="indeterminate"
                v-if="sending"
              ></md-progress-spinner>
            </div>

            <md-card-actions class="md-layout md-gutter md-alignment-center">
              <md-button
                type="submit"
                class="md-raised md-accent btnEntrar"
                :disabled="sending"
              >Entrar</md-button>
            </md-card-actions>
            <md-snackbar :md-active.sync="showSnackbar">{{alertMessage}}</md-snackbar>
          </form>
        </div>
      </md-card-content>
    </md-card>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength
} from "vuelidate/lib/validators";

export default {
  name: "Login",
  data: () => ({
    form: {
      email: null,
      password: null
    },
    sending: false,
    alertMessage: '',
    showSnackbar: false,
  }),
  validations: {
    form: {
      password: {
        required
      },
      email: {
        required,
        email
      }
    }
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty
        };
      }
    },
    clearForm() {
      this.$v.$reset();
      this.form.password = null;
      this.form.email = null;
    },
    saveUser() {
      this.sending = true;
      const baseURI = this.$baseURL + "menudigital/index.php/login/validar_datos";
      this.$http.post(baseURI,{ correo: this.form.email, contrasena: this.form.password },{"Access-Control-Allow-Origin": "*"})
      .then(res => {this.$router.push('/home');
        //this.users = result.data;
        console.log(res);
        if(res.data.status === "OK"){
          localStorage.setItem("tokenUser_et", res.data.token);
          localStorage.setItem("id_marca_et", res.data.id_marca);
          localStorage.setItem("id_rol_et", res.data.id_rol);
          localStorage.setItem("nombreUser_et", res.data.nombre);
          
        }

        if(res.data.status === "ERROR"){
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sending = false;
        }

        if(res.data.status != "OK" && res.data.status != "ERROR"){
          this.alertMessage =  "Ocurrio un error al intentar obtener la información del servidor, Comuniquese con soporte";
          this.showSnackbar = true;
          this.sending = false;
        }
      });
    },
    validateUser() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveUser()
      }
    },
  }
};
</script>

